<template>
    <div>
        <!-- $emit('update-differentsong',different.id) -->
        <li class="hotview-song" @click="hh(different)">
            <a href.prev="">
                <!-- :class="{active: index<3}" -->
                <!-- .toString().padStart(2, 0) -->
                <div id="hotview-song-id">{{index+1}}</div>
                <div class="hotview-content">
                    <div class="hotview-content-left">
                        <div class="hotview-content-left-name">
                            {{different.name}}
                            <span v-if="different.alia[0]">({{different.alia[0]}})</span>
                        </div>
                        <div class="hotview-content-left-author">
                            <i v-if="different.sq !== null"></i>
                            {{different.ar[0].name}} <span v-if="different.ar[1]">/ {{different.ar[1].name}}</span>
                            <span v-if="different.ar[2]">/ {{different.ar[2].name}}</span>- {{different.al.name}}
                        </div>
                    </div>
                    <div class="hotview-content-right">
                        <span v-if=" different.id !== playID"></span>
                        <div v-else :class="{ playing: playing }" class="newsong-play active">
                            <span></span>
                            <span></span>
                            <span></span>
                            <span></span>
                        </div>
                    </div>
                </div>
            </a>
        </li>
    </div>
</template>

<script>
export default {
    props: {
        different: Object,
        index: Number,
        playID: [String, Number],
        playing: Boolean
    },
    methods:{
        hh(id){
            console.log(id);
            this.$emit('update-differentsong',id)
        }
    }
}
</script>

<style lang="less" scoped>
@keyframes playingsong {
    from {
        transform: rotateX(15deg);
    }

    to {
        transform: rotateX(60deg)
    }
}

.hotview-song {
    .active {
        color: #df3436;
    }

    a {
        color: #333;
        text-decoration: none;
        display: flex;
        padding: 10rem;

        #hotview-song-id {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 40rem;
            font-size: 17rem;
            color: #a1a4b3;
            font-weight: 700;
        }

        .hotview-content {
            display: flex;
            justify-content: space-between;
            flex: 1 1 auto;
            padding-left: 10rem;
            // margin-left: 10rem;
            // border-bottom: 1rem solid #efeff0;

            .hotview-content-left {
                padding: 6rem 0;
                // overflow: hidden;
                width: 0;
                flex: 1 1 auto;

                .hotview-content-left-name {
                    font-size: 17rem;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    word-break: normal;
                    font-weight: bold;

                    span {
                        color: #888;
                        margin-left: 4rem;
                        // overflow: hidden;
                    }
                }

                .hotview-content-left-author {
                    i {
                        background-image: url(../assets/song.png);
                        display: inline-block;
                        width: 12rem;
                        height: 8rem;
                        background-size: 166rem 97rem;
                    }

                    font-size: 12rem;
                    color: #888;
                }
            }

            .hotview-content-right {
                padding: 0 10rem;
                display: flex;
                align-items: center;

                span {
                    display: inline-block;
                    width: 22rem;
                    height: 22rem;
                    line-height: 22rem;
                    background-position: -24rem 0;
                    background-image: url(../assets/song.png);
                    background-size: 166rem 97rem;
                }

                .newsong-play {
                    display: flex;
                    justify-content: space-around;
                    width: 22rem;
                    height: 22rem;

                    &.playing {
                        span {
                            animation-play-state: running;
                        }
                    }

                    span {
                        width: 11%;
                        height: 100%;
                        background: #ffe31b;
                        animation: playingsong 0.8s infinite alternate;
                        animation-play-state: paused;
                        transform-origin: bottom;

                        &:nth-child(1) {
                            animation-delay: 0s;
                        }

                        &:nth-child(2) {
                            animation-delay: -0.2s;
                        }

                        &:nth-child(3) {
                            animation-delay: -0.4s;
                        }

                        &:nth-child(4) {
                            animation-delay: -0.6s;
                        }
                    }

                }

            }
        }

    }
}
</style>