<template>
    <div class="Play-list">
        <i class="return" @click="$router.go(-1)"><img src="../assets/.svg" alt=""></i>
        <div class="Play-list-top-father">
            <!-- <div v-if="playlist.coverImgUrl !=undefined" class="Play-list-top"
                :style="{backgroundImage: `url(${playlist.coverImgUrl}?imageView=1&type=webp&thumbnail=253x0)`}">
            </div> -->
            <div class="content">
                <div class="Play-list-top-fl">
                    <img v-if="playlist.coverImgUrl !=undefined"
                        :src="`${playlist.coverImgUrl}?imageView=1&type=webp&thumbnail=253x0`" alt="">
                    <span class="gd" v-if="!playlist.highQuality"></span>
                    <span class="gd" v-else></span>
                    <i>
                        <div class="bf"></div>{{(playlist.playCount / 10000).toFixed(0)}}
                    </i>
                </div>
                <div class="Play-list-top-fr">
                    <h2>{{playlist.name}}</h2>
                    <div class="author">
                        <img v-if="playlist.creator?.avatarUrl !=undefined"
                            :src="`${playlist.creator?.avatarUrl}?imageView=1&type=webp&thumbnail=61x0`" alt="">
                        <!-- <i v-if="playlist.creator?.avatarDetail.identityIconUrl !=undefined"
                            :style="{backgroundImage:` url(${playlist.creator?.avatarDetail.identityIconUrl}`}"></i> -->
                    </div>
                    <span class="author-name">{{playlist.creator?.nickname}}</span>
                </div>
            </div>
        </div>
        <div v-if="ShowHiddenlocading" class="loacding">
            <div class="newsong-play active">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
            </div>
            <div class="loading-text"> ...</div>
        </div>
        <div class="pylst-list-bottom">
            <h3></h3>
            <ul class="list" v-for="(song,index) in playlist.tracks?.slice(0,10)" :key="song.id">
                <SongList :song="song" :index="index" @update-song="$emit('update-song',$event)" :playing="playing"
                    :playID="playID"></SongList>
            </ul>
            <div class="bottom">
                
            </div>
        </div>
        <div class="pylst-comment">
            <h4></h4>
            <ul v-for="playcomment in playcomments" :key="playcomment.userId">
                <PylstComment :playcomment="playcomment"></PylstComment>
            </ul>
        </div>
    </div>
</template>

<script>
import SongList from '@/components/SongList.vue'
import PylstComment from '@/components/PylstComment.vue'

export default {

    // encodeURIComponent(window.location.href);
    components: {
        SongList,
        PylstComment,
    },
    props: {
        playing: Boolean,
        playID: Number
    },
    data: function () {
        return {
            playlist: [],
            playcomments: [],
            ShowHiddenlocading: false,
        };
    },
    methods: {
        go() {
            console.log(123);
        },

        getPlayList: function (id) {
            this.axios
                .get("https://apis.netstart.cn/music/playlist/detail", {
                    params: { id },
                })
                .then((res) => {
                    this.ShowHiddenlocading = false
                    this.playlist = res.data.playlist
                });

        },
        getPlayComment: function (id) {
            this.axios
                .get("https://apis.netstart.cn/music/comment/playlist", {
                    params: { id },
                })
                .then((res) => {
                    this.playcomments = res.data.hotComments
                });
        }
    },
    created: function () {
        this.ShowHiddenlocading = true
        this.getPlayList(this.$route.query.id)
        this.getPlayComment(this.$route.query.id)
    }

}
</script>

<style lang="less" scoped>
.Play-list {
    .return {
        img{
           width: 20rem;
           height: 20rem; 
        }
        color: black;
        position: absolute;
        top: 5rem;
        left: 10rem;
        // margin-top: 20rem;
        // margin-left: 20rem;
        font-size: 20rem;
        font-weight: bold;
        font-style: normal;
        z-index: 20;
    }

    .Play-list-top-father {
        position: relative;
        padding: 30rem 10rem 30rem 15rem;
        overflow: hidden;
    }

    .Play-list-top {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50%;
        filter: blur(20rem);
        transform: scale(1.5);
    }

    .content {
        display: flex;
        position: relative;
        z-index: 2;



        .Play-list-top-fl {
            width: 126rem;
            height: 126rem;
            position: relative;

            img {
                border-radius: 20rem;
            }

            span.gd {
                position: absolute;
                z-index: 3;
                top: 18rem;
                left: 0rem;
                padding: 0 8rem;
                height: 17rem;
                // color: #fff;
                color: #272439;
                font-size: 9rem;
                text-align: center;
                line-height: 17rem;
                // background-color: rgba(217, 48, 48, .8);
                background-color: #ffe31b;
                border-top-right-radius: 17rem;
                border-bottom-right-radius: 17rem;
            }

            i {
                font-style: normal;
                position: absolute;
                left: 17rem;
                bottom: 4px;
                z-index: 3;
                padding-left: 15rem;
                color: #fff;
                font-size: 12rem;
                background-position: 0;
                background-repeat: no-repeat;
                background-size: 11rem 10rem;
                text-shadow: 1rem 0 0 rgb(0 0 0 / 15%);

                // background-image: url(../assets/home-card-item.svg);
                .bf {
                    background-image: url(../assets/.png);
                    width: 14rem;
                    height: 18rem;
                    // background-position: 0;
                    background-size: cover;
                    position: relative;
                    top: 15px;
                    left: -17px;
                }
            }
        }

        .Play-list-top-fr {
            flex: 1 1 auto;
            width: 1%;
            margin: 0 16rem;

            h2 {
                // padding-top: 1rem;
                margin-top: 25rem;
                font-weight: bold;
                font-size: 17rem;
                line-height: 1.3;
                // color: #fefefe;
                color: #272439;
                height: 44rem;
                display: -webkit-box;
                -webkit-box-pack: center;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                margin-bottom: 8rem;
            }

            .author {
                display: inline-block;
                width: 30rem;
                height: 30rem;
                border-radius: 50%;
                vertical-align: middle;
                margin-right: 5rem;
                position: relative;
                // margin-top: 20rem;

                img {
                    border-radius: 50%;
                    width: 100%;
                    vertical-align: middle;

                }

                i {
                    position: absolute;
                    right: -5rem;
                    bottom: 0;
                    width: 12rem;
                    height: 12rem;
                    background-repeat: no-repeat;
                    background-size: cover;
                }
            }

            .author-name {
                // color: hsla(0, 0%, 100%, .7);
                color: #272439;
                // margin-top: 20rem;
            }
        }
    }

    .pylst-list-bottom {
        position: relative;
        z-index: 998;

        h3 {
            height: 23rem;
            line-height: 23rem;
            padding: 0 10rem;
            font-size: 18rem;
            font-weight: bold;
            color: black;
            // background-color: #eeeff0;
            margin-bottom: 10rem;
        }

        .bottom {
            color: #999;
            font-size: 14rem;
            text-align: center;
        }

    }

    .pylst-comment {
        h4 {
            height: 23rem;
