            margin-top: 20rem;

        }
    }

    .loacding {
        background-color: white;
        border-radius: 10rem;
        box-shadow: 0 0 15rem rgba(0, 0, 0, .1);
        width: 75rem;
        height: 60rem;
        text-align: center;
        position: absolute;
        z-index: 999;
        left: 152.5rem;



        .newsong-play {
            display: flex;
            justify-content: space-around;
            width: 22rem;
            margin-left: 25rem;
            height: 22rem;


        }

        span {
            position: relative;
            width: 11%;
            height: 100%;
            background: #ffe31b;
            animation: playingsong 0.8s infinite alternate;
            // animation-play-state: paused;
            transform-origin: bottom;
            margin-top: 5rem;

            &:nth-child(1) {
                animation-delay: -0.2s;
            }

            &:nth-child(2) {
                animation-delay: -0s;
            }

            &:nth-child(3) {
                animation-delay: -0.2s;
            }

            &:nth-child(4) {
                animation-delay: 0s;
            }
        }

        .loading-text {
            margin-top: 10rem;
            font-size: 12rem;
            color: gray;
            padding: 0 2rem;
        }

    }
}
</style>