<template>
    <div class="Search-view">
        <!-- <i class="return" @click.stop="$router.go(-1)"></i> -->
        <div class="Search-view-input-father">
            <div class="Search-view-input">
                <i></i>
                <input v-focus type="text" placeholder="" v-model="value" ref="losefouse" @keyup.enter="keyupEnter"
                    @focus="focusseaction">
                <div class="close">
                    <i v-show="value" @click="empty"></i>
                </div>
                <!-- <div class="line"></div> -->
            </div>
            <span class="return" @click.stop="$router.go(-1)"></span>
        </div>
        <div v-if="ShowHidden" class="loacding">
            <div class="newsong-play active">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
            </div>
            <div class="loading-text"> ...</div>
        </div>
        <!-- <div v-if="ShowHidden" class="loacding"><img src="../assets/download.gif" alt=""></div> -->
        <!-- <div class="search-song" v-if="three"> -->
        <div class="search-song" v-if="albumsongs">
            <!-- playID="playsong?.id" :playing="playing" -->
            <searchSong :albumsongs="albumsongs" :album="album" @update-searchsong="$emit('update-searchsong',$event)"
                :playID="playID" :playing="playing">
            </searchSong>
        </div>
        <!-- v-if="searchResult" -->
        <div v-if="searchResult?.length && albumsongs.length === 0 && value && show" class="section-view-list">
            <h3 @click="getSearchResult(value);inexecution=false; suggest=false; albumsongs=[]">
                {{value}}</h3>
            <ul>
                <li v-for=" (searchcard,index) in  searchResult" :key="index"
                    @click="getSearchResult(searchcard.keyword); value=searchcard.keyword ; inexecution=false; suggest=false; albumsongs=[]">
                    <i></i>
                    <span>{{searchcard.keyword}}</span>
                </li>
            </ul>
        </div>
        <div class="default" v-show="!value">
            <div class="section-view-hotsong">
                <h3></h3>
                <ul>
                    <searchHotsongName v-for="(SearchHot,index) in SearchHots" :index="index" :key="index"
                        :SearchHot="SearchHot" @click-search="clickSearch">
                    </searchHotsongName>
                </ul>
            </div>
            <ul class="search-history">
                <searchHistory v-for="(memory,index) in memorys" :key="index" :memory="memory" :index="index"
                    @delete-history="deleteHistory" @search-history="clickSearch"></searchHistory>
            </ul>
        </div>

    </div>
</template>

<script>
import searchHotsongName from '@/components/searchHotsongName.vue';
import searchHistory from '@/components/searchHistory.vue';
import searchSong from '@/components/searchSong.vue';
var _ = require("lodash");


export default {
    props: {
        playID: [String, Number],
        playing: Boolean
    },
    components: {
        searchHotsongName,
        searchHistory,
        searchSong,
    },
    data: function () {
        return {
            // three: false,
            // two: false,
            ShowHidden: false,
            skipSong: false,
            value: "",
            searchResult: [],
            SearchHots: [],
            memorys: JSON.parse(localStorage.getItem("memorys")) || [],
            album: null,
            albumsongs: [],
            inexecution: false,
            // suggest: true,
            show: false,
        }
    },
    methods: {
        keyupEnter() {
            if (this.value) {
                this.getSearchResult(this.value);
                this.inexecution = false;
                this.suggest = true;
                this.three = true;
                this.two = true
                this.show = false
            }

        },
        // keyup() {
        //     this.suggest = true
        //     if (this.value) {
        //         this.two = true;
        //     }
        //     // this.two = true;

        // },
        empty: function () {
            this.value = ""
            // this.ShowHidden = false
            this.albumsongs = []
            this.album = null
            this.two = false
            this.inexecution = true
        },
        deleteHistory(index) {
            this.memorys = this.memorys.filter((o, i) => i !== index)
        },
        getSearchSuggests: function (keywords) {
            this.ShowHidden = true

            this.axios
                .get("https://apis.netstart.cn/music/search/suggest", {
                    params: { keywords: keywords, type: "mobile" }
                })
                .then((res) => {
                    this.ShowHidden = false
                    this.searchResult = (res.data.code != 400) ? res.data.result.allMatch : []
                })

        },
        getSearchResult: function (keywords) {

            this.$refs.losefouse.blur()
            this.ShowHidden = true
            this.memorys = [this.value, ...this.memorys.filter((o) => o != this.value)]
            Promise.all([this.axios.get("https://apis.netstart.cn/music/search/multimatch", {
                params: { keywords }
            }),
            this.axios.get("https://apis.netstart.cn/music/cloudsearch", {
                params: { keywords }
            })]).then(([p, n]) => {
                this.album = p.data.result ? p.data.result : []
                this.ShowHidden = false
                if (!this.inexecution) {
                    this.albumsongs = n.data.result != undefined ? n.data.result.songs : []
                }
            })
            // .then((res) => {
            //     console.log(res);

            // }),
            // this.axios.get("https://apis.netstart.cn/music/cloudsearch", {
            //     params: { keywords }
            // }).then((res) => {

            // })
        },
        clickSearch(o) {
            this.inexecution = false;
            this.suggest = false;
            // this.suggest = true;
            this.three = true;
            // this.two = false
            this.value = o;
            this.getSearchResult(o)
            this.show = false
        },
        focusseaction() {
            // console.log(123);
            this.albumsongs = [];
            this.inexecution = true;
            this.suggest = true;
            this.album = null;
            this.show = true
            // if (this.value) {
            //     this.two = true;
            //     this.three = false
            // }
            // this.noSuggest = false;
            // if (this.searchResult?.length != 0) {
            //     this.ShowHidden = false
            // }

        },

    },
    watch: {
        value: function (newvalue) {
            if (newvalue) {
                // if (this.suggest) {
                // console.log(123);
                this.debouncedGetSearchSuggests(newvalue);
                // }
            } else {
                // console.log(111);
                this.searchResult = []
                // this.two = false
                // this.three = false
            }
        },
        memorys: function (newmemory) {
            localStorage.setItem("memorys", JSON.stringify(newmemory));
        },
    },
    // computed: {
    //     getsuggest() {
    //         if (this.value) {
    //             if (this.suggest) {
    //                 console.log(123);
    //                 return this.debouncedGetSearchSuggests(this.value);
    //             }
    //         } else {
    //             return this.searchResult = []
    //         }
    //     }
    // },


    created() {
        this.ShowHidden = true
        this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
            this.ShowHidden = false
            this.SearchHots = res.data.result.hots;
        });

        this.debouncedGetSearchSuggests = _.debounce(this.getSearchSuggests, 200);
    },


}
</script>

<style lang="less" scoped>
.Search-view {
    margin-top: 60rem;
    position: relative;
    // border-bottom: 1rem solid rgba(0, 0, 0, .1);

    i.return {
        font-size: 30rem;
        font-weight: bold;
        color: #ffe31b;
        position: absolute;
        top: -22rem;
        left: 7rem;
    }

    .Search-view-input-father {
        padding: 15rem 10rem;
        display: flex;
    }

    .Search-view-input {
        position: relative;
        width: 90%;
        height: 30rem;
        padding: 0 30rem;
        box-sizing: border-box;
        background: #ebecec;
        border-radius: 30rem;




        .line {
            background-color: rgba(0, 0, 0, .1);
            width: 120%;
            height: 1rem;
            top: 50rem;
            left: -10rem;
            position: absolute;
        }

        i {
            position: absolute;
            left: 0;
            top: 9rem;
            margin: 0 8rem;
            vertical-align: middle;
            // color: #ccc;
            background-image: url(../assets/search-view-search.svg);
            width: 13rem;
            height: 13rem;
            // display: inline-block;
            // vertical-align: middle;
            // background-position: 0 0;
            // background-size: contain;
            // background-repeat: no-repeat;
        }

        input {
            width: 100%;
            height: 30rem;
            line-height: 18rem;
            background: rgba(0, 0, 0, 0);
            font-size: 14rem;
            color: #333;
            // border-radius: 0;
            border: none;
            outline: 0;

        }

        input::-webkit-input-placeholder {
            color: #c9c9c9;
            font-size: 14rem;
        }


        .close {
            position: absolute;
            right: 0;
            top: 0;
            width: 30rem;
            height: 30rem;
            line-height: 28rem;
            text-align: center;

            i {
                width: 14rem;
                height: 14rem;
                background-position: 0 0;
                background-size: contain;
                background-repeat: no-repeat;
                background-image: url(../assets/search-view.svg);
            }
        }
    }

    span.return {
        display: flex;
        justify-content: center;
        align-items: center;
        color: gray;
        margin-left: 5rem;
    }

    .section-view-list {
        margin: 0 auto;

        h3 {
            margin-top: 20rem;
            height: 50rem;
            margin-left: 10rem;
            padding-right: 10rem;
            font-size: 15rem;
            line-height: 50rem;
            color: #507daf;
            width: 100%;
            border-bottom: 1rem solid rgba(0, 0, 0, .1);
        }

        ul {
            li {
                display: flex;
                align-items: center;
                height: 45rem;
                padding-left: 10rem;
                width: 100%;
                border-bottom: 1rem solid rgba(0, 0, 0, .05);

                i {
                    background-image: url(../assets/search-view-search.svg);
                    width: 15rem;
                    height: 15rem;
                    display: inline-block;
                    vertical-align: middle;
                    background-position: 0 0;
                    background-size: contain;
                    background-repeat: no-repeat;
                }

                span {
                    display: inline-block;
                    flex: 1;
                    width: 1%;
                    padding-right: 10rem;
                    font-size: 15rem;
                    line-height: 45rem;
                    color: #333;
                    margin-left: 5rem;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    word-break: normal;
                }
            }
        }
    }

    .section-view-hotsong {
        // padding: 30rem 10rem;
        padding: 20rem 10rem 10rem 10rem;


        h3 {
            font-size: 18rem;
            line-height: 12rem;
            // color: #666;
            font-weight: bold;
        }

        ul {
            margin: 10px 0 7px;
        }
    }

    // .loacding {
    //     position: absolute;
    //     display: flex;
    //     align-items: center;
    //     justify-content: center;
    //     width: 100%;
    //     height: 100%;
    //     background-color: white;
    // }
    .loacding {
        background-color: white;
        border-radius: 10rem;
        box-shadow: 0 0 15rem rgba(0, 0, 0, .1);
        width: 75rem;
        height: 60rem;
        text-align: center;
        position: absolute;
        z-index: 999;
        left: 152.5rem;



        .newsong-play {
            display: flex;
            justify-content: space-around;
            width: 22rem;
            margin-left: 25rem;
            height: 22rem;


        }

        span {
            position: relative;
            width: 11%;
            height: 100%;
            background: #ffe31b;
            animation: playingsong 0.8s infinite alternate;
            // animation-play-state: paused;
            transform-origin: bottom;
            margin-top: 5rem;

            &:nth-child(1) {
                animation-delay: -0.2s;
            }

            &:nth-child(2) {
                animation-delay: -0s;
            }

            &:nth-child(3) {
                animation-delay: -0.2s;
            }

            &:nth-child(4) {
                animation-delay: 0s;
            }
        }

        .loading-text {
            margin-top: 10rem;
            font-size: 12rem;
            color: gray;
            padding: 0 2rem;
        }

    }

}
</style>