                    <span></span>
                    <span></span>
                </div>
                <div class="loading-text"> ...</div>
            </div>
            <!-- <div v-if="ShowHiddenlocading" class="loacding"><img src="../assets/download.gif" alt=""></div> -->
            <DifferentListview v-for="(different,index) in differents?.tracks?.slice(0,10)" :key="different.id"
                :different="different" :playID="playID" :playing="playing" :index="index"
                @update-differentsong="$emit('update-differentsong',$event)" />
            <!-- @update-song="$emit('update-song',$event)" -->
        </ul>
        <div class="bottom">
            <span></span>
        </div>
    </div>
</template>
    
<script>
//   import soaringView from '@/components/soaringView.vue'
import DifferentListview from '@/components/DifferentListview.vue'
//   DifferentListview
export default {
    components: {
        //   soaringView,
        DifferentListview,
    },
    props: {
        playID: [String, Number],
        playing: Boolean
    },
    data: function () {
        return {
            ShowHiddenlocading: false,
            // Allplaylists: [],
            updatetime: "",
            differents: [],
        }
    },
    methods: {
        getdiffernt(id) {
            this.axios.get("https://apis.netstart.cn/music/playlist/detail", {
                params: { id }
            }).then((res) => {
                this.ShowHiddenlocading = false
                // console.log(res.data);
                this.differents = res.data.playlist
            })
        }
    },
    created() {
        // console.log(this.$route.query.id);
        // this.ShowHiddenlocading = true;
        this.getdiffernt(this.$route.query.id)


    },
}

</script>
    
