<template>
    <div class="playbar" @click="$router.push({ path: '/playPage', query: { id: playsong.id } })">
        <img v-if="playsong?.picUrl" :class="{playing: playing}"
            :src="playsong ? `${playsong.picUrl}?imageView=1&type=webp&thumbnail=187.5x0` : '' " alt="">
        <img v-if="playsong?.al" :class="{playing: playing}"
            :src="playsong ? `${playsong.al.picUrl}?imageView=1&type=webp&thumbnail=187.5x0` : '' " alt="">
        <div class="content">
            <h3>{{playsong?.name}}</h3>
            <h6>{{String(parseInt(currentTime / 60)).padStart(2, "0")}}:
                {{String(parseInt(currentTime %60)).padStart(2,"0")}} /
                {{String (parseInt(duration/60)).padStart(2,"0")}}:{{String(parseInt(duration%60)).padStart(2,"0")}}
            </h6>
        </div>
        <div v-if="!playing" @click.stop="$emit('song-play')" class="stop">
            <p></p>
        </div>
        <div v-else class="play" @click.stop="$emit('song-pause')">
            <p>||</p>
        </div>
        <div class="list">
            <p @click.stop="displaylists">|||</p>
            <ul v-if="displaylist">
                <h6 class="my"></h6>
                <li :class="{pl:mysonglist.id === playsong.id}" v-for="(mysonglist,o) in mysonglists" :key="o"
                    @click.stop="$emit('update-playbarsong',mysonglist)">
                    <div class="fr"> {{mysonglist.name}}
                        <div v-if="mysonglist?.song" class="author">{{mysonglist.song.artists[0].name}}</div>
                        <div v-if="mysonglist?.ar" class="author"> {{mysonglist.ar[0].name}}</div>
                    </div>
                    <div class="fl" @click="$emit('delects-song',mysonglist)">x</div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {

    props: {
        playsong: Object,
        playing: Boolean,
        duration: Number,
        currentTime: Number,
        mysonglists: Array
    },
    data: function () {
        return {
            displaylist: false
        }
    },
    methods: {
        displaylists() {
            this.displaylist ? this.displaylist = false : this.displaylist = true
        }
    },
    watch: {
        playsong(newplaysong) {
            console.log(newplaysong.id)
        }
    }
}
</script>

<style lang="less" scoped>
@keyframes rotate {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.pl {
    background-color: rgba(230, 222, 222, 0.379);
}

.playbar {
    position: fixed;
    justify-content: space-around;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 999;
    // background-color: #d43c33;
    background: #e4cd1d;
    height: 60rem;
    display: flex;
    padding: 10rem;
    border-radius: 20px;

    img {
        border-radius: 50%;
        animation: rotate 8s linear infinite;
        animation-play-state: paused;
        width: 40rem;
        height: 40rem;

        &.playing {
            animation-play-state: running;
        }
    }

    .content {
        margin-left: 10rem;
        width: 0;
        flex: 1 1 auto;

        h3 {
            color: white;
            font-size: 16rem;
            font-weight: 700;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            word-break: normal;
        }

        h6 {
            color: white;
            font-size: 12rem;
        }
    }


    .stop,
    .play {
        width: 40rem;
        height: 40rem;
        background-color: white;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        margin-left: 50rem;

        p {
            font-size: 16rem;
            color: white;
            width: 30rem;
            height: 30rem;
            border-radius: 50%;
            text-align: center;
            line-height: 30rem;
            padding-left: 4rem;
            // background-color: #d43c33;
            background: #e4cd1d;
        }
    }

    .play {
        p {
            font-weight: bold;
            padding-left: 0rem;
            line-height: 27rem;
        }
    }

    .list {
        p {
            transform: rotate(90deg);
            color: white;
            width: 30rem;
            height: 30rem;
            font-weight: bold;
            font-size: 18rem;
            padding: 0 25rem;
            line-height: 20rem;
        }

        h6.my {
            font-size: 20rem;
            color: black;
            font-weight: bold;
            padding: 5rem 10rem;
        }

        ul {

            padding: 20rem;
            position: absolute;
            width: 100%;
            height: 300px;
            top: -300rem;
            left: 0;
            background-color: white;
            // overflow: hidden;
            overflow: scroll;
            border-radius: 20px;
            box-shadow: 0 0 15px rgba(0, 0, 0, 0.25);
            padding: 20rem;
            position: absolute;
            width: 95%;
            height: 300px;
            top: -300rem;
            left: 8px;
            background-color: white;
            overflow: scroll;
            border-radius: 20px;
            box-shadow: 0 0 15px rgb(0 0 0 / 25%);


            li {
                display: flex;
                justify-content: space-between;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                word-break: normal;
                padding: 5rem 10rem;

                .fr {
                    color: black;
                    font-size: 17rem;

                    .author {
                        color: #888;
                        font-size: 14rem;
                    }
                }


                .fl {
                    color: black;
                    font-size: 20rem;
                    font-style: normal;

                }

            }
        }
    }

}
</style>