<template>
    <div class="song-item" @click="$emit('update-song',newsong)">
        <!-- @click="$router.push({ path: '/playPage', query: { id: newsong.id } })" -->
        <a href.prevent="">
            <span class="no">{{index+1}}</span>
            <div class="song-item-left">
                <div class="song-name">{{newsong.name}} <span
                        v-if="newsong.song.alias[0]">{{newsong.song.alias[0]}}</span>
                </div>
                <div class="autor">
                    <i v-if="newsong.song.sqMusic !== null"></i>
                    {{newsong.song.artists[0].name}} <span v-if="newsong.song.artists[1]">/
                        {{newsong.song.artists[1].name}}</span> <span v-if="newsong.song.artists[2]">/
                        {{newsong.song.artists[2].name}}</span> -
                    {{newsong.song.album.name}}
                </div>
            </div>
            <div  class="song-item-right">
                <span v-if=" newsong.id !== playID"></span>
                <div v-else :class="{ playing: playing }" class="newsong-play active">
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                </div>
            </div>
        </a>
    </div>
</template>

<script>
export default {
    props: {
        newsong: Object,
        playID: [String, Number],
        playing: Boolean,
        index:Number,
    }
}
</script>

<style lang="less" scoped>
@keyframes playingsong {
    from {
        transform: rotateX(15deg);
    }

    to {
        transform: rotateX(60deg)
    }
}

.song-item {

    a {
        color: #333;
        text-decoration: none;
        display: flex;
        justify-content: space-between;
        flex: 1 1 auto;
        margin-left: 10rem;
        padding: 10rem;
        // border-bottom: 1rem solid #efeff0;

        .no{
            display: flex;
            align-items: center;
            justify-content: center;
            width: 40rem;
            font-size: 17rem;
            color: #a1a4b3;
            font-weight: 700;
        }

        .song-item-left {
            padding: 6rem 0;
            // width: 0;
            width: 0;
            flex: 1 1 auto;


            .song-name {
                font-size: 17rem;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                word-break: normal;
                font-weight: bold;

                span {
                    color: #888;
                }
            }

            .autor {
                font-size: 12rem;
                color: #888;

                i {
                    background-image: url(../assets/song.png);
                    display: inline-block;
                    width: 12rem;
                    height: 8rem;
                    margin-right: 4rem;
                    background-size: 166rem 97rem;
                }
            }
        }

        .song-item-right {
            padding: 0 10rem;
            display: flex;
            align-items: center;

            span {
                display: inline-block;
                width: 22rem;
                height: 22rem;
                background-position: -24rem 0;
                background-image: url(../assets/song.png);
                background-size: 166rem 97rem;
            }

            .newsong-play {
                display: flex;
                justify-content: space-around;
                width: 22rem;
                height: 22rem;

                &.playing {
                    span {
                        animation-play-state: running;
                    }
                }

                span {
                    width: 11%;
                    height: 100%;
                    background: #ffe31b;
                    animation: playingsong 0.8s infinite alternate;
                    animation-play-state: paused;
                    transform-origin: bottom;

                    &:nth-child(1) {
                        animation-delay: 0s;
                    }

                    &:nth-child(2) {
                        animation-delay: -0.2s;
                    }

                    &:nth-child(3) {
                        animation-delay: -0.4s;
                    }

                    &:nth-child(4) {
                        animation-delay: -0.6s;
                    }
                }

            }

        }
    }

    
}
</style>