        <PlayPagelyric v-if="!fullsong" :songtime="songtime" :text="text" :playsong="playsong"
            :currentTime="currentTime" :duration="duration" />
        <!-- <transition name="fade">
            <div :style="{xy:xian}" v-show="xian" v-if="model === ''  " class="modelshow"></div>
            <div :style="{xy:xian}" v-show="xian" v-if="model === ''  " class="modelshow"></div>
            <div :style="{xy:xian}" v-show="xian" v-if="model === ''  " class="modelshow"></div>
        </transition> -->


