            </h6>

            <!-- <label for=""> -->
            <!-- audio.currentTime = (tmpCurrentTime * duration) / 100 -->
            <!-- `(${jindu}*${duration})/100` -->

            <ProgressBar :currentTime="currentTime" :duration="duration" @update-time="$emit('update-time', $event)" />

            <!-- <input class="progressBar" type="range" ref="input" :max="duration" :value="tmpCurrentTime"
            @input.stop="draging = true; " @change="
              draging = false;
              $emit('update-time', $event.target.value );
            "> -->
            <!-- :style="`width:${tmpCurrentTime}%`" -->
            <!-- @input="pullJINdu" @change.stop="
              draging = false;
              $emit('update-time', $event.target.value);
            " -->
            <!-- <div class="jindu" > -->
            <!-- (this.currentTime / this.duration) * 100 + "%" -->
            <!-- "`width:(${tmpCurrentTime} / ${duration}) * 100 + %`"> -->
            <!-- this.tmpCurrentTime / this.duration * 100 -->
            <!-- <div class="mask" ref="mask"></div> -->
            <!-- </div> -->
            <!-- </label> -->
            <!-- <h6>{{String(parseInt(currentTime / 60)).padStart(2, "0")}}:
            {{String(parseInt(currentTime %60)).padStart(2,"0")}} /
            {{String (parseInt(duration/60)).padStart(2,"0")}}:{{String(parseInt(duration%60)).padStart(2,"0")}}
        </h6> -->
            <h5> {{ String(parseInt(duration / 60)).padStart(2, "0") }}:{{ String(parseInt(duration % 60)).padStart(2,
                    "0")
            }}
            </h5>
