</template>

<script>
import PlayPagelyric from '@/components/PlayPagelyric.vue'
import FullCreen from '@/components/FullCreen.vue'
import ProgressBar from '@/components/ProgressBar.vue'

export default {
    components: {
        PlayPagelyric,
        FullCreen,
        ProgressBar,
    },
    props: {
        playsong: Object,
        playing: Boolean,
        duration: Number,
        currentTime: Number,
        mysonglists: Array,
    },
    data() {
        return {
            singpic: "",
            playings: this.playing,
            lyric: [],
            songtime: [],
            text: [],
            index: JSON.parse(localStorage.getItem("newindex")) || 0,
            tmpCurrentTime: this.currentTime,
            // draging: false,
            models: ['', '', ''],
            model: JSON.parse(localStorage.getItem("newmodel")) || '',
            displaylist: false,
            fullsong: false,
            xian: true,
            ShowHiddenlocading: false,
        }
    },
    // computed: {
    //     jindu() {
    //         return this.tmpCurrentTime / this.duration
    //     }
    // },
    methods: {
        displaylists() {
            this.displaylist ? this.displaylist = false : this.displaylist = true
        },
        listclick(n) {
            this.$emit('update-playbarsongs', n)
            this.getlyric(n.id)
            this.getsingpic(n.id);
        },
        getsingpic(ids) {
            // this.ShowHiddenlocading=true
            this.axios
                .get("https://apis.netstart.cn/music/song/detail", {
                    params: { ids },
                })
                .then((res) => {
                    // this.ShowHiddenlocading = false
                    this.singpic = res.data.songs;
                });
        },
        getlyric(id) {
            this.axios
                .get("https://apis.netstart.cn/music/lyric", {
                    params: { id },
                })
                .then((res) => {
                    var arr = res.data.lrc.lyric.split("\n")
                    var parsed = arr
                        .filter((o) => o)
                        .map((o) => {
                            var { groups } = o.match(
                                /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/
                            );
                            return {
                                time: groups.mm * 60 + groups.ss * 1,
                                text: groups.text.trim(),
                            };
                        });
                    this.lyric = parsed
                    // console.log(this.lyric);
                    this.lyric.forEach(o => {
                        this.songtime.push(o.time)
                        this.text.push(o.text)
                    })
                });

        },
        clickPaused() {
            // console.log(123);
            this.playings = !this.playings
            this.$emit("change-status", this.playings)
        },
        // clickrun(){
        //     this.$emit("change-status", this.playings)
        // },
        modelchange() {
            if (!this.xian) {
                this.xian = true
            }
            // this.xian = true
            var subscript = this.models.findIndex((o) => o == this.model)
            this.model = subscript >= (this.models.length - 1) ? this.models[0] : this.models[subscript + 1]
            setTimeout(() => {
                // console.log(1);
                this.xian = false
            }, 20)
            // this.xian = true
        },
        prev() {
            switch (this.model) {
                case "":
                    this.index--
                    if (this.index < 0) {
                        this.index = this.mysonglists.length - 1
                    }
                    break;
                case "":
                    console.log("sui");
                    console.log(this.index);
                    var randonlist = Math.floor(Math.random() * this.mysonglists.length)
                    if (randonlist === this.index) {
                        randonlist = Math.floor(Math.random() * this.mysonglists.length)
                        this.index = randonlist
                    } else {
                        this.index = randonlist
                    }
                    break;

                case "":
                    console.log("dan");
                    var dansong = this.index;
                    this.$emit('update-time', 0)
                    this.index = dansong;
                    break;
            }
            this.getlyric(this.mysonglists[this.index].id)
            this.getsingpic(this.mysonglists[this.index].id);
            this.$emit('next-song', this.mysonglists[this.index])
        },

        next() {
            switch (this.model) {
                case "":
                    this.index++
                    if (this.index > this.mysonglists.length - 1) {
                        this.index = 0
                    }
                    break;
                case "":
                    console.log("sui");
                    var randonlists = Math.floor(Math.random() * this.mysonglists.length)
                    console.log(randonlists);
                    if (randonlists === this.index) {
                        console.log("");
                        randonlists = Math.floor(Math.random() * this.mysonglists.length)
                        this.index = randonlists
                    } else {
                        this.index = randonlists
                    }
                    console.log(this.index, randonlists);
                    break;
                case "":
                    var dansongs = this.index;
                    this.$emit('update-time', 0)
                    this.index = dansongs;
                    break;
            }

            this.getlyric(this.mysonglists[this.index].id)
            this.getsingpic(this.mysonglists[this.index].id);
            this.$emit('next-song', this.mysonglists[this.index])
        },
    },
    created() {
        if (this.$route.query.id != this.mysonglists[this.index].id) {
            // console.log(456);
            this.getsingpic(this.mysonglists[this.index].id);
            this.getlyric(this.mysonglists[this.index].id)
        } else {
            this.getsingpic(this.$route.query.id);
            this.getlyric(this.$route.query.id)
        }

    },
    beforeRouteUpdate(to, from, next) {
        this.getsingpic(to.query.id);
        this.getlyric(this.$route.query.id)
        next();
    },
    watch: {
        index(newindex) {
            localStorage.setItem("newindex", JSON.stringify(newindex));
        },
        playsong() {
            this.songtime = []
            this.text = []
        },
        currentTime: function (newcurrentTime) {
            // console.log(newcurrentTime,this.$refs.input.value);
            // this.$refs.mask.style.width = newcurrentTime /this.duration *100 +"%"
            // this.$refs.input.value =  newcurrentTime /this.duration *100 +"%"
            // if (!this.draging) {
            //     this.tmpCurrentTime = newcurrentTime;
            // }


            if (newcurrentTime == this.duration) {
                console.log(123);
                switch (this.model) {
                    case "":
                        console.log(1);
                        this.index++
                        if (this.index > this.mysonglists.length - 1) {
                            this.index = 0
                        }
                        break;
                    case "":
                        console.log(2);
                        var randonlistss = Math.floor(Math.random() * this.mysonglists.length)
                        console.log(randonlistss);
                        if (randonlistss === this.index) {
                            console.log("");
                            randonlistss = Math.floor(Math.random() * this.mysonglists.length)
                            this.index = randonlistss
                        } else {
                            this.index = randonlistss
                        }
                        break;
                    case "":
                        var dansongs = this.index;
                        this.$emit('update-dqtime', 0)
                        this.index = dansongs;
                        break;
                }
                this.getlyric(this.mysonglists[this.index].id)
                this.getsingpic(this.mysonglists[this.index].id);
                this.$emit('next-song', this.mysonglists[this.index])
            }

        },
        // tmpCurrentTime: function (newtmpCurrentTime) {
        //     this.jindu = newtmpCurrentTime / this.duration * 100
        // },
        model(newmodel) {
            // this.$emit('model-updata',newmodel)
            localStorage.setItem("newmodel", JSON.stringify(newmodel))
        }
    }
}
</script>

