<template>
    <div class="lyric">
        <!-- <h2>{{playsong?.name}} -
            <span v-if="playsong?.song">{{playsong?.song?.artists[0].name}}</span>
            <span v-if="playsong?.ar"> {{playsong.ar[0].name}}</span>
        </h2> -->
        <div class="hud" ref="hua">
            <ul :style="{transform:`translateY(-${la}rem)`}" ref="ul">
                <li :class="{gl:i === b}" v-for="(t,i) in text" :key="i">{{t}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        text: Array,
        songtime: Array,
        playsong: Object,
        duration: Number,
        currentTime: Number,
    },
    data() {
        return {
            reversongtime: [],
            la: 0,
            arr: [],
            b: 0,
            c: 0,
        }
    },
    mounted() {
        // console.log(this.$refs.ul);
        // console.log(this.$refs.ul.querySelectorAll("li"));
        var a = [...this.$refs.ul.querySelectorAll("li")]
        // console.log(a);
        a.map(o => {
            // console.log(o);
            this.arr.push(o.clientHeight)
        })
    },
    computed: {
        // change(){
        //     return this.la += this.arr[this.b]
        // }
    },
    watch: {
        currentTime() {
            var a = [...this.$refs.ul.querySelectorAll("li")]
            // console.log(a); 
            this.arr = []
            a.map(o => {
                // console.log(o);
                this.arr.push(o.clientHeight)
            })
            // console.log(this.arr);
            // var c = this.arr.map((o) => o)
            // sum = sum + arr[index]
            var arr = this.songtime
            this.reversongtime = [...arr].reverse()
            this.b = this.reversongtime.length - 1 - this.reversongtime.findIndex((o) => o < this.currentTime) == -1 ? 0 :
                (this.reversongtime.length - 1 - this.reversongtime.findIndex((o) => o < this.currentTime))
            // console.log(this.la);

            // var sum = 0

            // var b = (this.reversongtime.length - 1 - this.reversongtime.findIndex((o) => o < this.currentTime))
            // console.log(b ,this.la);
            // this.$refs.hua.style.height = this.arr[b] + "rem"
            // this.$refs.ul.style.height = this.arr[b] + "rem"
            // console.log(this.reversongtime);
            // console.log(this.songtime);
            // this.reversongtime.length

            // this.arr.map((o) =>
            //     this.la = this.reversongtime.length - 1 - this.reversongtime.findIndex((o) => o < this.currentTime) == -1 ? 0 :
            //         (this.reversongtime.length - 1 - this.reversongtime.findIndex((o) => o < this.currentTime)) * o
            // )
            // console.log(this.reversongtime.length - 1 - this.reversongtime.findIndex((o) => o < this.currentTime));
            // console.log(this.b, this.text[this.b], this.arr[this.b]);
            this.c = this.arr.slice(0, this.b)
            // console.log('==========', this.c);
            var total = 0
            this.c.forEach(o => {
                total += o
            });
            this.la = total
        },

    }


}
</script>

<style lang="less" scoped>
.lyric {
    position: relative;
    z-index: 10;
    padding: 0 30rem;
    margin: 0 auto;
    

    h2 {
        text-align: center;
        font-size: 18rem;
        line-height: 1.1;
        color: #fefefe;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        height: 33rem;

        span {
            font-size: 16rem;
            color: hsla(0, 0%, 100%, .6);
        }
    }

    .hud {
        height: 30rem;
        overflow: hidden;
        width: 300rem;
        margin-top: 30rem;
        padding: 0 10rem;
        height: 39rem;
        margin: 30rem auto;


        ul {
            padding-top: 21rem ;
            transition: all .3s;

            li {
                color: rgb(255, 255, 255);
                // width: 300rem;
                // height: 21rem;
                // width: 250rem;
                text-align: center;
                font-size: 14rem;
                overflow: hidden;
                &.gl{
                    color: rgb(249, 222, 28);
                }
            }
        }
    }


}
</style>