<template>
    <div class="search-song" v-if="albumsongs?.length">
        <ul class="bestrecommend" v-show=" album?.orders[0]">
            <h3></h3>
            <li class="sing" v-if="album?.artist">
                <img v-if="album?.artist[0]"
                    :src="`${album?.artist[0].img1v1Url}?imageView=1&type=webp&thumbnail=101x0`" alt="">
                <div class="content">
                    <h4>: <p>{{album?.artist[0].name}} <span
                                v-if="album.artist[0].alias[0]">({{album?.artist[0].alias[0]}})</span></p>
                    </h4>
                </div>
                <span class="left"></span>
            </li>
            <li class="sing" v-if="album?.album">
                <img v-if="album?.album" :src="`${album?.album[0].picUrl}?imageView=1&type=webp&thumbnail=101x0`"
                    alt="">
                <div class="content">
                    <h4>
                        <p>{{album?.album[0].name}}</p>
                    </h4>
                    <p class="by">{{album?.album[0].artist.name}}</p>
                </div>
                <span class="left"></span>
            </li>
            <li class="sing" v-if="album?.playlist">
                <img v-if="album?.playlist"
                    :src="`${album?.playlist[0].coverImgUrl}?imageView=1&type=webp&thumbnail=101x0`" alt="">
                <div class="content">
                    <h4>
                        <p>{{album?.playlist[0].name}}</p>
                    </h4>
                    <p class="by">by {{album?.playlist[0].creator.nickname}}</p>
                </div>
                <span class="left"></span>
            </li>
        </ul>
        <ul class="albumsong">
            <li v-for="albumsong in albumsongs" :key="albumsong.id" @click="$emit('update-searchsong',albumsong)">
                <div class="search-song-fl">
                    <div class="name"> {{albumsong.name}}</div>
                    <div class="author"><i v-show="albumsong.sq"></i> {{albumsong.ar[0].name}}
                        <span v-if="albumsong.ar[1]">/ {{albumsong.ar[1].name}}</span>
                        <span v-if="albumsong.ar[2]">/ {{albumsong.ar[2].name}}</span>
                        - {{albumsong.al.name}}
                    </div>
                </div>
                <div class="search-song-ri">
                    <span v-if=" albumsong.id !== playID"></span>
                    <div v-else :class="{ playing: playing }" class="newsong-play active">
                        <span></span>
                        <span></span>
                        <span></span>
                        <span></span>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props: {
        albumsongs: Array,
        album: Object,
        playID: [String, Number],
        playing: Boolean,
    }
}
</script>

<style lang="less" scoped>
@keyframes playingsong {
    from {
        transform: rotateX(15deg);
    }

    to {
        transform: rotateX(60deg)
    }
}

.search-song {
    margin-top: 10rem;

    .bestrecommend {

        h3 {
            margin-left: 10rem;
            font-size: 12rem;
            line-height: 16rem;
            color: #666;
        }

        .sing {
            display: flex;
            align-items: center;
            height: 66rem;
            margin-left: 10rem;
            padding: 8rem 10rem 8rem 0;
            box-sizing: border-box;
            border-bottom: 1rem solid #efeff0;

            img {
                position: relative;
                width: 50rem;
                height: 50rem;
                margin-right: 15rem;
                line-height: 0;
            }

            .content {
                flex: 1;
                display: inline-block;
                width: 1%;

                h4 {
                    font-size: 17rem;
                    line-height: 30rem;
                    color: #333;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    word-break: normal;

                    p {
                        display: inline;
                    }
                }

                .by {
                    color: #888;
                }
            }

            span.left {
                margin-right: 8rem;
                margin-left: 10rem;
                width: 8rem;
                height: 13rem;
                background-image: url(../assets/left.svg);

            }
        }
    }

    ul.albumsong {
        li {
            display: flex;
            justify-content: space-between;
            flex: 1 1 auto;
            margin-left: 10rem;
            border-bottom: 1rem solid #efeff0;

            .search-song-fl {
                padding: 6rem 0;
                width: 0;
                flex: 1 1 auto;

                .name {
                    font-size: 17rem;
                    // color: #507daf;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    word-break: normal;

                }

                .author {
                    i {
                        background-image: url(../assets/song.png);
                        display: inline-block;
                        width: 12rem;
                        height: 8rem;
                        margin-right: 4rem;
                        background-size: 166rem 97rem;
                        // background-position:  0rem 0rem;
                    }

                    font-size: 12rem;
                    // color: #507daf;
                    color: #888;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    word-break: normal;
                }
            }

            .search-song-ri {
                padding: 0 10rem;
                display: flex;
                align-items: center;

                span {
                    display: inline-block;
                    width: 22rem;
                    height: 22rem;
                    background-position: -24rem 0;
                    background-image: url(../assets/song.png);
                    background-size: 166rem 97rem;


                }

                .newsong-play {
                    display: flex;
                    justify-content: space-around;
                    width: 22rem;
                    height: 22rem;

                    &.playing {
                        span {
                            animation-play-state: running;
                        }
                    }

                    span {
                        width: 11%;
                        height: 100%;
                        background: #ffe31b;
                        animation: playingsong 0.8s infinite alternate;
                        animation-play-state: paused;
                        transform-origin: bottom;

                        &:nth-child(1) {
                            animation-delay: 0s;
                        }

                        &:nth-child(2) {
                            animation-delay: -0.2s;
                        }

                        &:nth-child(3) {
                            animation-delay: -0.4s;
                        }

                        &:nth-child(4) {
                            animation-delay: -0.6s;
                        }
                    }

                }
            }
        }
    }

}
</style>