<template>
    <div class="stagefather">
        <div class="stage"  >
            <ul ref="imgBox" @transitionend="animateEnd" @touchstart="touchstart" @touchmove="touchmove"
                @touchend="touchend">
                <li @click="$router.push({path:'/palylist',query:{id:imgArr[imgArr.length-1].id}})"><img :src="imgArr[imgArr.length-1].img" alt=""></li>
                <li @click="$router.push({path:'/palylist',query:{id:item.id}})" v-for="(item, index) in imgArr" :key="index">
                    <img :src="item.img" alt="">
                </li>
                <li @click="$router.push({path:'/palylist',query:{id:imgArr[0].id}})"><img :src="imgArr[0].img" alt=""></li>
            </ul>
        </div>
        <div class="circle">
            <ul>
                <li :class="{active: index === circleIndex }" v-for="(item,index) in imgArr" :key="index"></li>
            </ul>
        </div>
    </div>
</template>

<script>
import one from '@/assets/1111.jpg'
import two from '@/assets/2222.jpg'
import three from '@/assets/3333.jpg'

export default {

    data() {
        return {
            imgArr: [
                { "id": 7611987415, img: one },
                { "id": 360062344, img: two , },
                { "id": 7584508479, img: three },
            ],
            flag: true,
            circleIndex: 0,
            imgIndex: 0,
            imgWidth: 0,
            interval: 2000,
            startX: 0,
            moveX: 0,
            timer: null,
        }
    },
    methods: {
        animateEnd() {
            if (this.imgIndex >= this.imgArr.length) {
                this.imgIndex = 0;
                this.$refs.imgBox.style.transition = "none";
                this.$refs.imgBox.style.transform = `translateX(-${this.imgWidth}px)`;
            }
            if (this.imgIndex < 0) {
                this.imgIndex = this.imgArr.length - 1;
                this.$refs.imgBox.style.transition = "none";
                this.$refs.imgBox.style.transform = `translateX(${this.trX}px)`;
            }
            this.flag = true;
        },
        touchstart(event) {
            clearInterval(this.timer);
            if (this.flag) {
                this.startX = event.targetTouches[0].clientX;
            }
        },
        touchmove(event) {
            if (this.flag) {
                this.moveX = event.targetTouches[0].clientX - this.startX;
                this.$refs.imgBox.style.transition = 'none';
                this.$refs.imgBox.style.transform = `translateX(${this.trX + this.moveX}px)`;
            }
        },
        touchend() {
            if (this.flag) {
                if (this.moveX > 80) {
                    this.imgIndex--;
                    this.circleIndex--;
                } else if (this.moveX < -80) {
                    this.imgIndex++;
                    this.circleIndex++;
                }
                this.$refs.imgBox.style.transition = 'all .5s';
                this.$refs.imgBox.style.transform = `translateX(${this.trX}px)`;
            }
            clearInterval(this.timer);
            this.timer = setInterval(() => {
                this.imgIndex++;
                this.circleIndex++;
                this.$refs.imgBox.style.transition = 'all .5s';
                this.$refs.imgBox.style.transform = `translateX(${this.trX}px)`;
            }, this.interval);
            this.flag = false;
        }
    },
    watch: {
        circleIndex() {
            if (this.circleIndex >= this.imgArr.length) {
                this.circleIndex = 0;
            }
            if (this.circleIndex < 0) {
                this.circleIndex = this.imgArr.length - 1;
            }
        },
        // imgIndex() {
        //     if (this.imgIndex >= this.imgArr.length) {
        //         this.imgIndex = 0;
        //     }
        //     if (this.imgIndex < 0) {
        //         this.imgIndex = this.imgArr.length - 1;
        //     }
        // }
    },
    computed: {
        trX() {
            return -(this.imgWidth + this.imgWidth * this.imgIndex);
        }



    },
    mounted() {
        this.imgWidth = window.innerWidth;
        this.$refs.imgBox.style.transform = `translateX(-${this.imgWidth}px)`;
        this.timer = setInterval(() => {
            this.imgIndex++;
            this.circleIndex++;
            this.$refs.imgBox.style.transition = "all .5s";
            this.$refs.imgBox.style.transform = `translateX(${this.trX}px)`;
            // console.log( this.$refs.imgBox);
        }, this.interval)
    },
    destroyed() {
        clearInterval(this.timer)
    }


}


</script>

<style lang="less" scoped>
.stagefather {
    // margin: 0 auto;
    width: 324rem;

    .stage {
        display: flex;
        width: 366rem;
        overflow: hidden;

        ul {
            display: flex;
            // width: 324rem;
            width: 1910rem;
            justify-content: space-around;
            // transition: all .5s;

            li {
                width: 342rem;
                margin: 0 16.5rem;

                img {
                    width: 342rem;
                    border-radius: 10rem;

                }
            }

        }

    }

    .circle {
        position: absolute;
        top: 171rem;
        left: 169rem;

        ul {
            display: flex;
            justify-content: center;
            align-items: center;

            li {
                width: 6rem;
                height: 6rem;
                background-color: grey;
                margin: 10rem 5rem;
                border-radius: 50%;
                opacity: 0.8;

                &.active {
                    background-color: white;
                }
            }
        }


    }
}
</style>