import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import HotView from '../views/HotView.vue'
import SearchView from '../views/SearchView.vue'
import PlayListView from '../views/PlayListView.vue'
import PlayPageView from "../views/PlayPage.vue"
import NewSong from "../views/NewSong.vue"
import soaringSong from "../views/soaringSong.vue"
import ListView from "../views/ListView.vue"
import DifferentList from "../views/DifferentList.vue"

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: HomeView,
    meta: { showNav: true, showbar: true }
  },
  {
    path: '/hot',
    component: HotView,
    meta: { showNav: true, showbar: true }

    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // component: () => import(/* webpackChunkName: "about" */ '../views/HotView.vue')
  }, {
    path: "/search",
    component: SearchView,
    meta: { showNav: true, showbar: true }
  }, {
    path: "/palylist",
    component: PlayListView,
    meta: { showbar: true }
  }, {
    path: "/PlayPage",
    component: PlayPageView
  }, {
    path: "/NewSong",
    component: NewSong,
    meta: { showNav: true, showbar: true }
  }, {
    path: "/soaringSong",
    component: soaringSong,
    meta: { showNav: true, showbar: true }
  }, {
    path: "/ListView",
    component:  ListView,
    meta: { showNav: true, showbar: true }
  },{
    path: "/DifferentList",
    component:  DifferentList,
    meta: { showNav: true, showbar: true }
  }
 
]

const router = new VueRouter({
  routes
})

export default router
