<script>
import HomeCard from '@/components/HomeCard.vue'
import HomeFooter from '@/components/homeFooter.vue'
import SlideShow from '@/components/SlideShow.vue'

export default {
  name: 'HomeView',
  components: {
    HomeCard,
    HomeFooter,
    SlideShow,
  },
  props: {
    playID: [String, Number],
    playing: Boolean
  },

  data: function () {
    return {
      personalized: [],
      newsongs: [],
      timeLineHeight: "",
      showlocading: false,
      hotsongs: [],
      soaringsongs: [],
    }
  },

  // beforeRouteEnter() {
  //   this.showlocading = true
  // },

  created() {
    this.showlocading = true
    Promise.all([this.axios.get("https://apis.netstart.cn/music/personalized", {
      params: { limit: 6 },
    }),
    this.axios.get("https://apis.netstart.cn/music/personalized/newsong")])
      .then(([p, n]) => {
        this.personalized = p.data.result;
        this.newsongs = n.data.result;
      })
    this.axios
      .get("https://apis.netstart.cn/music/playlist/detail?id=19723756")
      .then((res) => {
        this.showlocading = false
        this.hotsongs = res.data.playlist.tracks
      });
    this.axios
      .get("https://apis.netstart.cn/music/playlist/track/all?id=1")
      .then((res) => {
        // this.showlocading = false
        this.soaringsongs = res.data.songs
      });
