import sed from '@/assets/222.png'
import trd from '@/assets/333.png'
import fourth from '@/assets/444.png'
import fifth from '@/assets/555.png'
import sixth from '@/assets/666.png'
import seventh from '@/assets/777.png'
import eighth from '@/assets/888.png'
import nineth from '@/assets/999.png'

export default {
    data: function () {
        return {
            personalized: [],
            newsongs: [],
            // timeLineHeight: "",
            // showlocading: false,
            hotsongs: [],
            soaringsongs: [],
            updatetime: "",
            imgArr: [
                { "id": 1978921795, img: one },
                { "id": 3001835560, img: two },
                { "id": 21845217, img: three },
                { "id": 6723173524, img: four },
                { "id": 7325478166, img: five },
                { "id": 991319590, img: six },
                { "id": 5059642708, img: seven },
                { "id": 71384707, img: eight },
                { "id": 5453912201, img: nine },
                { "id": 3779629, img: ten },
                { "id": 71385702, img: tenone },
                { "id": 10520166, img: tentwo },
                { "id": 5338990334, img: tenthree },
                { "id": 6732051320, img: twofour },
                // { "id": 7584508479, img: three },
            ],
            imgArrs: [
                { "id": 1978921795, img: first },
                { "id": 3001835560, img: sed },
                { "id": 21845217, img: trd },
                { "id": 6723173524, img: fourth },
                { "id": 991319590, img: fifth },
                { "id": 10520166, img: sixth },
                { "id": 6732051320, img: seventh },
                { "id": 5338990334, img: eighth },
                { "id": 71385702, img: nineth },
                // { "id": 7584508479, img: three },
            ],
        }
    },
    methods: {
        click(id) {
            // console.log(id);
            this.$router.push({ path: '/DifferentList', query: { id: id } })
        }
    },
    created() {
        this.showlocading = true
        Promise.all([this.axios.get("https://apis.netstart.cn/music/personalized", {
            params: { limit: 6 },
        }),
        this.axios.get("https://apis.netstart.cn/music/personalized/newsong")])
            .then(([p, n]) => {
                // this.showlocading = false
                this.personalized = p.data.result;
                this.newsongs = n.data.result;
            })
        this.axios
            .get("https://apis.netstart.cn/music/playlist/detail?id=3778678")
            .then((res) => {
                // this.showlocading = false
                this.hotsongs = res.data.playlist.tracks
            });
        this.axios
            .get("https://apis.netstart.cn/music/playlist/track/all?id=1")
            .then((res) => {
                // this.showlocading = false
                this.soaringsongs = res.data.songs
            });
        this.axios.get("https://apis.netstart.cn/music/playlist/detail?id=3778678")
            .then((res) => {
                this.updatetime = new Date(res.data.playlist.updateTime).toLocaleString().slice(0, 9).split("/")
            })
        if (this.personalized && this.newsongs && this.hotsongs && this.soaringsongs) {
            console.log("true");
        }
    },
}
</script>

