<template>
    <div class="creen" @click="changfull">
        <div class="background" v-if="singpic" :style="{backgroundImage:` url(${singpic[0]?.al?.picUrl}`}">
            <div class="mask"></div>
        </div>
        <div class="huds" ref="hua">
            <!-- :style="{transform:`translateY(-${la}rem)`}" -->
            <ul ref="ul" :style="{transform:`translateY(-${la}rem)`}">
                <li ref="li" v-for="(t,i) in text" :key="i" :class="{active: i === b }">{{t}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
// import { text } from 'body-parser';

// import { watch } from 'vue';

export default {
    props: {
        duration: Number,
        currentTime: Number,
        songtime: Array,
        text: Array,
        singpic: Array,
    },
    data() {
        return {
            arr: [],
            c: [],
            b: 0,
            la: 0,
        }
    },
    methods: {
        changfull() {
            console.log(123);
            this.$emit('chang-full')
        }
    },
    mounted() {
        // console.log(this.$refs.ul);
        // console.log(this.$refs.ul.querySelectorAll("li"));
        var a = [...this.$refs.ul.querySelectorAll("li")]
        // console.log(a);
        a.map(o => {
            // console.log(o);
            this.arr.push(o.clientHeight)
        })
    },
    watch: {
        currentTime() {
            var a = [...this.$refs.ul.querySelectorAll("li")]
            // console.log(a); 
            this.arr = []
            a.map(o => {
                this.arr.push(o.clientHeight)
            })
            var arr = this.songtime
            this.reversongtime = [...arr].reverse()
            this.b = this.reversongtime.length - 1 - this.reversongtime.findIndex((o) => o < this.currentTime) == -1 ? 0 :
                (this.reversongtime.length - 1 - this.reversongtime.findIndex((o) => o < this.currentTime))
            this.c = this.arr.slice(0, this.b)
            // console.log('==========', this.c);
            var total = 0
            this.c.forEach(o => {
                total += o
            });
            this.la = total

        },
    }
}

</script>

<style lang="less" scoped>
.active {
    color: #ffe31b;
    ;
}

.creen {
    top: -28rem;
    width: 375rem;
    height: 426rem;
    position: relative;
    z-index: 10;
    background-color: rgb(93, 93, 189);

    .background {
        position: relative;
        opacity: 1;
        transform: scale(1.5);
        filter: blur(30px);
        background-color: #000000;
        background-position: 50%;
        background-repeat: no-repeat;
        background-size: auto 100%;
        transform-origin: center;
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        height: 100%;
        overflow: hidden;
        z-index: 1;

        .mask {
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: #252a4095;
        }
    }

    .huds {
        position: relative;
        // top: 150rem;
        // height: 517rem;
        overflow: hidden;
        text-align: center;
        z-index: 10;
        height: 463rem;

        ul {
            position: relative;
            top: 300rem;
            height: 500rem;
            color: rgba(255, 255, 255, 0.689);
            z-index: 10;
            transition: all .3s;

            li {
                font-size: 16rem;
                // margin: 5rem 0;
            }
        }

    }


}
</style>