<template>
    <li class="song-item" @click.stop="$emit('update-song',song)">
        <div class="no">{{index+1}}</div>
        <div class="content">
            <div class="fl">
                <div class="name">{{song.name}}</div>
                <div class="author">{{song.ar[0].name}} - {{song.al.name}}</div>
            </div>
            <div class="fr">
                <!-- <span></span> -->
                <span v-if=" song.id !== playID"></span>
                <div v-else :class="{ playing: playing }" class="newsong-play active">
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                </div>
            </div>
        </div>
    </li>
</template>

<script>
export default {
    props: {
        song: Object,
        index: Number,
        playing: Boolean,
        playID: Number,
    }
}
</script>

<style lang="less" scoped>
@keyframes playingsong {
    from {
        transform: rotateX(15deg);
    }

    to {
        transform: rotateX(60deg)
    }
}

i.return {
    font-size: 30rem;
    font-weight: bold;
    color: #ffe31b;
    position: absolute;
    top: -22rem;
    left: 7rem;
}

.song-item {
    display: flex;
    padding: 5rem;


    .no {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40rem;
        font-size: 17rem;
        color: #999;
        // margin-left: -10rem;
    }

    .content {
        display: flex;
        justify-content: space-between;
        flex: 1 1 auto;
        margin-left: 10rem;
        // border-bottom: 1rem solid #efeff0;

        .fl {
            padding: 6rem 0;
            width: 0;
            flex: 1 1 auto;

            .name {
                font-weight: bold;
                font-size: 17rem;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                word-break: normal;
            }

            .author {
                font-size: 12rem;
                color: #888;

            }
        }

        .fr {

            padding: 0 10rem;
            display: flex;
            align-items: center;

            span {
                display: inline-block;
                width: 22rem;
                height: 22rem;
                background-position: -24rem 0;
                background-image: url(../assets/song.png);
                background-size: 166rem 97rem;


            }

            .newsong-play {
                display: flex;
                justify-content: space-around;
                width: 22rem;
                height: 22rem;

                &.playing {
                    span {
                        animation-play-state: running;
                    }
                }

                span {
                    width: 11%;
                    height: 100%;
                    background: red;
                    animation: playingsong 0.8s infinite alternate;
                    animation-play-state: paused;
                    transform-origin: bottom;

                    &:nth-child(1) {
                        animation-delay: 0s;
                    }

                    &:nth-child(2) {
                        animation-delay: -0.2s;
                    }

                    &:nth-child(3) {
                        animation-delay: -0.4s;
                    }

                    &:nth-child(4) {
                        animation-delay: -0.6s;
                    }
                }

            }
        }
    }
}
</style>