</template>

<script>
export default {
    props: {
        duration: Number,
        currentTime: Number,
    },
    data() {
        return {
            tmpCurrentTime: this.currentTime,
            draging: false,
            // updatetime:0,
        }
    },
    // mounted() {
    //     this.$refs.mask.style.width = this.currentTime / this.duration * 100 + "%"
