    watch: {
        currentTime(newcurrentTime) {
            if (!this.draging) {
                this.tmpCurrentTime = newcurrentTime;
            }
            // this.$refs.mask.style.width = newcurrentTime / this.duration * 100 + "%"
        }
    }
}
</script>

